import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'
import Qa from '@/views/Qa.vue'
import CheckList from '@/views/CheckList.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/qa',
      name: 'QA',
      component: Qa
    },
    {
      path: '/checklist',
      name: 'CheckList',
      component: CheckList
    }
  ]
})



