<script>
export default {
  name: 'Qa',
  data () {
    return {
      picked1: '',
      picked2: '',
      q1notice: '*請作答*',
      q2notice: '*請作答*',
      ans1: true,
      ans2: true,
      showans: false,
      disabled: false
    }
  },
  created () {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    })
  },
  methods: {
    finished () {
      if (this.picked1 === '' && this.picked2 === '') {
        this.q1notice = '*尚未選取答案*'
        this.q2notice = '*尚未選取答案*'
        document.querySelector('#qa1').scrollIntoView({behavior: 'smooth', block: 'end', inline: 'nearest'})
      } else if (this.picked1 === '') {
        this.q1notice = '*尚未選取答案*'
        document.querySelector('#qa1').scrollIntoView({behavior: 'smooth', block: 'end', inline: 'nearest'})
      } else if (this.picked2 === '') {
        this.q2notice = '*尚未選取答案*'
        document.querySelector('#qa2').scrollIntoView({behavior: 'smooth', block: 'end', inline: 'nearest'})
      }
      if (this.picked1 !== '' && this.picked2 !== '') {
        this.q1notice = ''
        this.q2notice = ''
        document.querySelectorAll('input[type="radio"]').forEach(item => {
          item.disabled = true
        })
        if (this.picked1 !== '4') {
          this.ans1 = false
        }
        if (this.picked2 !== '4') {
          this.ans2 = false
        }
        this.disabled = true
        console.log(document.querySelector('.qa').scrollHeight)
        this.showans = true
        this.$nextTick(() => {
          this.submit()
        })
      }
    },
    submit () {
      let height = document.querySelector('.qa').scrollHeight
      window.scrollTo({
        top: height,
        behavior: 'smooth'
      })
    }
  }

}
</script>

