        { name: '34', title: 'qwrg' },
        { name: '1233', title: 'qwwge' },
        { name: '2343', title: 'qwwgre' }
      ],
      info: null
    }
  },
  created () {
    window.scrollTo({
      top: 0
    })
    // eslint-disable-next-line no-alert
    axios.get('https://api.coindesk.com/v1/bpi/currentprice.json').then(
      response => {
        this.info = response
        console.log(response)
      })
  },
  mounted () {
  },
  methods: {
    goToQA () {
      this.$router.push('qa')
    }
  }
}
